import React from 'react';
import styled from 'styled-components';

const Input = styled.input`
  &:checked {
 
  }
`;
handleChange = (e) => {
  props.checked
}
const CheckBox = (props) => (
  <div>
    <Input id="input" type="checkbox" onClick={props.handleClick} checked={props.checked} onChange={(e)=>this.handleChange(e)}/>
  </div>
);

export default CheckBox